<template>
	<view class="container">
		<u-popup v-model="show" mode="center" width="600rpx" border-radius="14">
			<view class="switch_box">
				<view class="title">请选择模板：</view>
					<radio-group @change="radioChange" class="radioGroup">
						<view class="radioBox" v-for="(item, index) in tempList" :key="item.id">
							<view>{{item.title}}</view>
							<radio :value="item.id" :checked="item.checked" style="transform:scale(0.7)" color="#03958C" />
						</view>
					</radio-group>
				<view class="footerBtn">
					<text class="cancel" @click="cancel">取消</text>
					<text @click="sure">确定</text>
				</view>
			</view>
		</u-popup>
	</view>
</template>

<script>
	import { bus } from '@/utils/bus.js';
	import httpUtils from '@/utils/httpUtils.js';
	import Tools from '@/utils/tools.js';
	export default {
		data() {
			return {
				show: false,
				tempList:[],
				value: '',
				// nowState:'', // 当前切换的身份 1销售经理
			}
		},
		methods: {
			// 获取模板列表
			_getList(){
				let that = this
				Tools.showLoading('加载中')
				httpUtils.sendHttpRequest('5001', Object.assign({}, ''), function (res) {
					Tools.hideLoading()
					if (res.data.code == '0') {
						that.show = true
						that.tempList = res.data.data
					}
				})
			},
			// 打开弹窗
			open(data) {
				this.nowState = data
				this._getList()
			},
			// 关闭弹窗
			cancel(){
				this.show = false
			},
			// 确定
			sure(){
				this.show = false
				uni.navigateTo({
					url: '/pages/salemanVisual/customer/questionList?id=' + this.value + '&cusId=' + this.nowState
				})
				// let that = this
				// Tools.showLoading('切换中')
				// httpUtils.sendHttpRequest('2040', Object.assign({}, {prjId:that.value}), function (res) {
				// 	Tools.hideLoading()
				// 	if (res.data.code == '0') {
				// 		let loginInfo = uni.getStorageSync('loginInfo')
				// 		loginInfo.defaultPrjId = that.value;
				// 		uni.setStorageSync('loginInfo', loginInfo);
				// 		Tools.showSuccessToast('切换成功')
				// 		setTimeout(function() {
				// 			if(that.nowState == 1){
				// 				uni.reLaunch({
				// 					url:'/pages/salemanagerVisual/case/index'
				// 				})
				// 			}else{
				// 				uni.reLaunch({
				// 					url:'/pages/salemanVisual/needDeal/index'
				// 				})
				// 			}
				// 		}, 500)
				// 	}
				// })
			},
			// 选中某个单选框时，由radio时触发
			radioChange(e) {
				this.value = e.detail.value
			},
		}
	}
</script>

<style lang="scss">
	@import '@/styles/poup.scss';
</style>